/* Modal Overlay */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  animation: fadeIn 0.3s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.modal-content {
  background: linear-gradient(135deg, #1e3c72 0%, #2a5298 100%);
  border: 1px solid rgba(0, 123, 255, 0.3);
  border-radius: 8px;
  padding: 2rem;
  max-width: 500px;
  width: 90%;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.5);
  animation: slideUp 0.3s ease-out;
}

@keyframes slideUp {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.modal-content h2 {
  margin-bottom: 0.5rem;
  color: #fff;
}

.modal-content p {
  color: #e0e0e0;
  margin-bottom: 1.5rem;
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  color: #e0e0e0;
  font-weight: 500;
}

.form-group input {
  width: 100%;
  padding: 0.7rem;
  background-color: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 4px;
  color: #e0e0e0;
  font-size: 1rem;
  transition: all 0.3s ease;
}

.form-group input:focus {
  outline: none;
  background-color: rgba(255, 255, 255, 0.15);
  border-color: rgba(0, 123, 255, 0.6);
  box-shadow: 0 0 8px rgba(0, 123, 255, 0.2);
}

.form-group input:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.form-group small {
  display: block;
  margin-top: 0.3rem;
  color: #aaa;
  font-size: 0.85rem;
}

.error-message {
  background-color: rgba(220, 53, 69, 0.2);
  color: #ff6b6b;
  padding: 0.8rem;
  border-left: 3px solid #dc3545;
  border-radius: 4px;
  margin-bottom: 1rem;
  font-size: 0.95rem;
}

.form-actions {
  display: flex;
  gap: 1rem;
  margin-top: 2rem;
}

.form-actions button {
  flex: 1;
  padding: 0.8rem;
  font-size: 1rem;
}

@media (max-width: 600px) {
  .modal-content {
    padding: 1.5rem;
  }
}

/* Seed Options */
.seed-options {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
  margin-bottom: 1rem;
}

.seed-option {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.seed-option input[type="radio"] {
  width: auto;
  margin: 0;
  cursor: pointer;
  accent-color: #007bff;
}

.seed-option label {
  margin: 0;
  font-weight: normal;
  color: #e0e0e0;
  cursor: pointer;
  display: inline;
}

.custom-seed-input {
  margin-top: 0.8rem;
  padding-left: 1.5rem;
  animation: slideDown 0.2s ease-out;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.custom-seed-input input {
  width: 100%;
  padding: 0.7rem;
  background-color: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 4px;
  color: #e0e0e0;
  font-size: 1rem;
  margin-bottom: 0.5rem;
  transition: all 0.3s ease;
}

.custom-seed-input input:focus {
  outline: none;
  background-color: rgba(255, 255, 255, 0.15);
  border-color: rgba(0, 123, 255, 0.6);
  box-shadow: 0 0 8px rgba(0, 123, 255, 0.2);
}

.custom-seed-input small {
  color: #aaa;
  font-size: 0.85rem;
}
